import Login from './views/Login.vue'
import NotFound from './views/404.vue'
import Home from './views/Home.vue'
import Main from './views/Main.vue'
import Reception from './views/nav1/Reception.vue'
import Order from './views/nav1/Order.vue'
import Classes from './views/nav2/Classes.vue'
import Instorage from './views/nav2/Instorage.vue'
import Product from './views/nav2/Product.vue'
import Selldetail from './views/nav2/Selldetail.vue'
import Sellstatistics from './views/nav2/Sellstatistics.vue'
import User from './views/nav3/User.vue'
import Userconsume from './views/nav3/Userconsume.vue'
import UserLevel from './views/nav3/UserLevel.vue'
import UserRecharge from './views/nav3/UserRecharge.vue'
import UserRule from './views/nav3/UserRule.vue'
import PlaceCharge from './views/nav4/PlaceCharge.vue'
import PlaceOpen from './views/nav4/PlaceOpen.vue'
import PlaceSet from './views/nav4/PlaceSet.vue'
import PlaceType from './views/nav4/PlaceType.vue'
import Finance from './views/nav5/Finance.vue'
import FinanceAPP from './views/nav5/FinanceAPP.vue'
import FinanceReport from './views/nav5/FinanceReport.vue'
import FinanceTrend from './views/nav5/FinanceTrend.vue'
import Statistics from './views/nav5/Statistics.vue'
import limited from './views/nav6/limited.vue'
import LimitedBank from './views/nav6/LimitedBank.vue'
import LimitedRole from './views/nav6/LimitedRole.vue'
import LimitedUser from './views/nav6/LimitedUser.vue'

let routes = [
    {
        path: '/login',
        component: Login,
        name: '',
        hidden: true
    },
    {
        path: '/404',
        component: NotFound,
        name: '',
        hidden: true
    },
    //{ path: '/main', component: Main },
    {
        path: '/',
        component: Home,
        name: '运营管理',
        iconCls: 'el-icon-message',//图标样式class
        children: [
            { path: '/main', component: Main, name: '主页', hidden: true },
            { path: '/Reception', component: Reception, name: '营业前台' },
            { path: '/Order', component: Order, name: '场地预约' },
        ]
    },
    {
        path: '/',
        component: Home,
        name: '商品管理',
        iconCls: 'fa fa-id-card-o',
        children: [
            { path: '/Classes', component: Classes, name: '添加商品类别' },
			{ path: '/Product', component: Product, name: '添加商品' },
            { path: '/Selldetail', component: Selldetail, name: '销售明细' },
			{ path: '/Sellstatistics', component: Sellstatistics, name: '销售统计' },
			{ path: '/Instorage', component: Instorage, name: '商品入库管理' }
        ]
    },
    {
    	path: '/',
    	component: Home,
    	name: '会员管理',
    	iconCls: 'fa fa-id-card-o',
    	children: [
    		{ path: '/User', component: User, name: '会员管理' },
    		{ path: '/UserRecharge', component: UserRecharge, name: '会员充值记录' },
			{ path: '/UserLevel', component: UserLevel, name: '会员级别设置' },
			{ path: '/UserRule', component: UserRule, name: '会员充值规则' },
			{ path: '/Userconsume', component: Userconsume, name: '会员消费查询' }
    	]
    },
	{
		path: '/',
		component: Home,
		name: '场地管理',
		iconCls: 'fa fa-id-card-o',
		children: [
			{ path: '/PlaceSet', component: PlaceSet, name: '场地设置' },
			{ path: '/PlaceType', component: PlaceType, name: '场地类型设置' },
			{ path: '/PlaceCharge', component: PlaceCharge, name: '场地收费设置' },
			{ path: '/PlaceOpen', component: PlaceOpen, name: '场馆营业时间设置' }
		]
	},
	{
		path: '/',
		component: Home,
		name: '财务管理',
		iconCls: 'fa fa-id-card-o',
		children: [
			{ path: '/Finance', component: Finance, name: '财务统计' },
			{ path: '/FinanceAPP', component: FinanceAPP, name: 'APP端回款统计' },
			{ path: '/FinanceReport', component: FinanceReport, name: '财务报表' },
			{ path: '/FinanceTrend', component: FinanceTrend, name: '报表趋势' },
			{ path: '/Statistics', component: Statistics, name: '报表统计' }
		]
	},
    {
        path: '/',
        component: Home,
        name: '系统管理',
        iconCls: 'fa fa-bar-chart',
        children: [
            { path: '/limited', component: limited, name: '营业场馆设置' },
			{ path: '/LimitedBank', component: LimitedBank, name: '收款银行账号设置' },
			{ path: '/LimitedRole', component: LimitedRole, name: '角色授权设置' },
			{ path: '/LimitedUser', component: LimitedUser, name: '管理员管理' }
        ]
    },
    {
        path: '*',
        hidden: true,
        redirect: { path: '/404' }
    }
];

export default routes;


