<template>
	<section>
		<!--工具条-->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true" :model="filters">
				<el-form-item>
					<el-button @click="limitedAdd = true" class="tools"><i class="el-icon-circle-plus-outline"></i>&nbsp;&nbsp;添加</el-button>
				</el-form-item>
				
				<el-form-item>
					<el-button class="tools" @click="batchRemove" :disabled="this.sels.length===0"><i class="el-icon-delete"></i>&nbsp;&nbsp;删除</el-button>
				</el-form-item>
				
				<el-form-item style="float:right;">
					<el-input v-model="filters.name" placeholder="卡号/手机号" style="width:300px;"></el-input>
					<el-button type="info" plain v-on:click="getUsers" class="searchkey">搜索</el-button>
				</el-form-item>
	
			</el-form>
		</el-col>

		<!--列表-->
		<el-table :data="users" stripe highlight-current-row v-loading="listLoading" @selection-change="selsChange" style="width: 100%;">
			<el-table-column type="index" width="60">
			</el-table-column>
			<el-table-column type="selection" width="55">
			</el-table-column>
			<el-table-column prop="name" label="卡号" sortable>
			</el-table-column>
			<el-table-column prop="sex" label="手机号" :formatter="formatSex" sortable>
			</el-table-column>
			<el-table-column prop="age" label="姓名" sortable>
			</el-table-column>
			<el-table-column prop="birth" label="注册日期" sortable>
			</el-table-column>
			<el-table-column prop="birth" label="会员类型" sortable>
			</el-table-column>
			<el-table-column prop="birth" label="有效日期" sortable>
			</el-table-column>
			<el-table-column prop="birth" label="营业场馆">
			</el-table-column>
			<el-table-column prop="birth" label="来源">
			</el-table-column>
			<el-table-column label="操作">
				<template scope="scope">
					<el-button class="tablebtn" size="small" @click="handleRole(scope.$index, scope.row)">充值</el-button>
					<span style="color: #7692cc;">|</span>
					<el-button class="tablebtn" size="small" @click="handleDel(scope.$index, scope.row)">消费记录</el-button>
				</template>
			</el-table-column>
		</el-table>

		<!--工具条-->
		<el-col :span="24" class="toolbar">
			<el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="20" :total="total" style="float:right;">
			</el-pagination>
		</el-col>

		<!--编辑界面-->
		<el-dialog title="编辑角色" :visible.sync="limitedEdit">
			<el-form :model="editForm" label-width="110px" :rules="editFormRules" ref="editForm" class="addform">
				<el-form-item label="角色名称" prop="name">
					<el-input v-model="addForm.name" auto-complete="off"></el-input>
				</el-form-item>
				
				<el-form-item label="编号" prop="name">
					<el-input v-model="addForm.name" auto-complete="off"></el-input>
				</el-form-item>
				
				<el-form-item label="适用场馆">
					<el-input v-model="addForm.name" auto-complete="off"></el-input>
				</el-form-item>
				
				<el-form-item label="备注">
					<el-input v-model="addForm.name" auto-complete="off"></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer" style="width: 95px;margin: 0px auto;">
				<el-button class="rear_bg" @click.native="editSubmit" :loading="editLoading">提交</el-button>
			</div>
		</el-dialog>

		<!--权限管理界面-->
		<el-dialog title="角色权限设置" :visible.sync="limitedRole">
			<el-form :model="editForm" label-width="110px" :rules="editFormRules" ref="editForm" class="addform">
				<el-form-item label="角色名称" prop="name">
					<el-input v-model="addForm.name" auto-complete="off"></el-input>
				</el-form-item>
				
				<el-form-item label="编号" prop="name">
					<el-input v-model="addForm.name" auto-complete="off"></el-input>
				</el-form-item>
				
				<el-form-item label="适用场馆">
					<el-input v-model="addForm.name" auto-complete="off"></el-input>
				</el-form-item>
				
				<el-form-item label="备注">
					<el-input v-model="addForm.name" auto-complete="off"></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer" style="width: 95px;margin: 0px auto;">
				<el-button class="rear_bg" @click.native="editSubmit" :loading="editLoading">提交</el-button>
			</div>
		</el-dialog>
		<!--新增界面-->
		<el-dialog title="新增角色" :visible.sync="limitedAdd">
			<el-form :model="addForm" label-width="110px" :rules="addFormRules" ref="addForm" class="addform">
				<el-form-item label="角色名称" prop="name">
					<el-input v-model="addForm.name" auto-complete="off"></el-input>
				</el-form-item>
				
				<el-form-item label="编号" prop="name">
					<el-input v-model="addForm.name" auto-complete="off"></el-input>
				</el-form-item>
				
				<el-form-item label="适用场馆">
					<el-input v-model="addForm.name" auto-complete="off"></el-input>
				</el-form-item>
				
				<el-form-item label="备注">
					<el-input v-model="addForm.name" auto-complete="off"></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer" style="width: 95px;margin: 0px auto;">
				<el-button class="rear_bg" @click.native="addSubmit" :loading="addLoading">提交</el-button>
			</div>
		</el-dialog>
	</section>
</template>

<script>
	import util from '../../common/js/util'
	//import NProgress from 'nprogress'
	import { getUserListPage, removeUser, batchRemoveUser, editUser, addUser } from '../../api/api';

	export default {
		data() {
			return {
				filters: {
					name: ''
				},
				users: [],
				total: 0,
				page: 1,
				listLoading: false,
				sels: [],//列表选中列

				editFormVisible: false,//编辑界面是否显示
				editLoading: false,
				editFormRules: {
					name: [
						{ required: true, message: '请输入姓名', trigger: 'blur' }
					]
				},
				//编辑界面数据
				editForm: {
					id: 0,
					name: '',
					sex: -1,
					age: 0,
					birth: '',
					addr: ''
				},

				limitedAdd: false,//添加界面是否显示
				limitedEdit: false,//添加界面是否显示
				limitedRole: false,//添加界面是否显示
				
				addLoading: false,
				addFormRules: {
					name: [
						{ required: true, message: '请输入姓名', trigger: 'blur' }
					]
				},
				//新增界面数据
				addForm: {
					name: '',
					sex: -1,
					age: 0,
					birth: '',
					addr: ''
				}

			}
		},
		methods: {
			//性别显示转换
			formatSex: function (row, column) {
				return row.sex == 1 ? '男' : row.sex == 0 ? '女' : '未知';
			},
			handleCurrentChange(val) {
				this.page = val;
				this.getUsers();
			},
			//获取用户列表
			getUsers() {
				let para = {
					page: this.page,
					name: this.filters.name
				};
				this.listLoading = true;
				//NProgress.start();
				getUserListPage(para).then((res) => {
					this.total = res.data.total;
					this.users = res.data.users;
					this.listLoading = false;
					//NProgress.done();
				});
			},
			//删除
			handleDel: function (index, row) {
				this.$confirm('确认删除该记录吗?', '提示', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					//NProgress.start();
					let para = { id: row.id };
					removeUser(para).then((res) => {
						this.listLoading = false;
						//NProgress.done();
						this.$message({
							message: '删除成功',
							type: 'success'
						});
						this.getUsers();
					});
				}).catch(() => {

				});
			},
			//显示编辑界面
			handleEdit(index, row) {
				this.limitedEdit = true;
				console.log(index, row);
			  },
			//显示权限管理界面
			handleRole(index, row) {
				this.limitedRole = true;
				console.log(index, row);
				},	
			//显示新增界面
			handleAdd: function () {
				this.addFormVisible = true;
				this.addForm = {
					name: '',
					sex: -1,
					age: 0,
					birth: '',
					addr: ''
				};
			},
			//编辑
			editSubmit: function () {
				this.$refs.editForm.validate((valid) => {
					if (valid) {
						this.$confirm('确认提交吗？', '提示', {}).then(() => {
							this.editLoading = true;
							//NProgress.start();
							let para = Object.assign({}, this.editForm);
							para.birth = (!para.birth || para.birth == '') ? '' : util.formatDate.format(new Date(para.birth), 'yyyy-MM-dd');
							editUser(para).then((res) => {
								this.editLoading = false;
								//NProgress.done();
								this.$message({
									message: '提交成功',
									type: 'success'
								});
								this.$refs['editForm'].resetFields();
								this.editFormVisible = false;
								this.getUsers();
							});
						});
					}
				});
			},
			//新增
			addSubmit: function () {
				this.$refs.addForm.validate((valid) => {
					if (valid) {
						this.$confirm('确认提交吗？', '提示', {}).then(() => {
							this.addLoading = true;
							//NProgress.start();
							let para = Object.assign({}, this.addForm);
							para.birth = (!para.birth || para.birth == '') ? '' : util.formatDate.format(new Date(para.birth), 'yyyy-MM-dd');
							addUser(para).then((res) => {
								this.addLoading = false;
								//NProgress.done();
								this.$message({
									message: '提交成功',
									type: 'success'
								});
								this.$refs['addForm'].resetFields();
								this.addFormVisible = false;
								this.getUsers();
							});
						});
					}
				});
			},
			selsChange: function (sels) {
				this.sels = sels;
			},
			//批量删除
			batchRemove: function () {
				var ids = this.sels.map(item => item.id).toString();
				this.$confirm('确认删除选中记录吗？', '提示', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					//NProgress.start();
					let para = { ids: ids };
					batchRemoveUser(para).then((res) => {
						this.listLoading = false;
						//NProgress.done();
						this.$message({
							message: '删除成功',
							type: 'success'
						});
						this.getUsers();
					});
				}).catch(() => {

				});
			}
		},
		mounted() {
			this.getUsers();
		}
	}

</script>

<style>

</style>


