        } else {
          if (this.regruleForm.regCheckpass !== '') {
            this.$refs.regruleForm.validateField('regCheckpass');
          }
          callback();
        }
      };
      var validatePass2 = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请再次输入密码'));
        } else if (value !== this.regruleForm.password) {
          callback(new Error('两次输入密码不一致!'));
        } else {
          callback();
        }
      };
			/*忘记密码判定规则*/
			var valiPass = (rule, value, callback) => {
				if (value === '') {
